.App {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;

  background-color: #ededed;

  overflow: hidden;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.days {
  line-height: 300px;
  font-size: 300px;
  color: black;

  font-weight: bold;

  letter-spacing: 0.15em;
  text-indent: 0.15em;

  transform: translateY(-0.12em);
}
