@font-face {
  font-family: "DeliciousHandrawn";
  src: url("./DeliciousHandrawn-Regular.ttf") format("truetype");
}

html,
body {
  margin: 0;
  padding: 0;

  width: 100%;
  height: 100vh;

  display: flex;
}

body {
  margin: 0;
  font-family: "DeliciousHandrawn", -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
  width: 100%;
  display: flex;
}
